import React from 'react'
import Link from 'gatsby-link'
import ComingSoon from '../img/gophers-coming-soon.png';

const ProjectsPage = () => (
    <div className="pc-main-wrapper">
        <div data-aos="fade-left" data-aos-duration="1000" className="pc-border"></div>
        <h1 data-aos="fade-in" data-aos-duration="1000" className="text-center">latest Projects</h1>
        <div data-aos="fade-right" data-aos-duration="1000" className="pc-border"></div>
        <h2 data-aos="fade-in" data-aos-duration="500" className="text-center" />
        <div className="notfound-wrapper" style={{ margin: 147 + 'px' }}>
            <img src={ComingSoon} style={{ width: 80 + '%' }} />
        </div>
    </div>
)

export default ProjectsPage


